import React, {useState, useEffect, forwardRef, useContext, useRef, useImperativeHandle} from 'react'
import { NCustSchemaForm } from '@/components/form'
import {getOrganizationTtree, getDictionaryOption} from '@/api/system'
import { mapTree } from '@/utils/index'
import { FormInstance } from 'antd/lib/form'
import {Context} from '../allDetail'
import moment from 'moment'
import {isCardNo} from '@/utils/validate'

const BaseInfor = forwardRef<any>((props, ref) => {
    const getContext = useContext(Context) // context
    const { modalType, resData }:any = getContext
    const formRef =  useRef<FormInstance>(null)

    const addSchema = {
        layout : {
            labelCol: { span: 8 },
            wrapperCol: { span: 12 },
        },
        grid: {
            rowGutter: 10,
            colSpan: 12
        },
        formItem: [
            {
                type: "tree-select",
                label: "",
                field: "organizationId",
                value: undefined,
                loading: true,
                props: {
                    placeholder: "",
                    treeDefaultExpandAll: false,
                },
                asyncOptions: async (curValue:any, fields:any) => {
                    const {result}:any = await getOrganizationTtree() 
                    let arr = mapTree(result[0])
                    return arr? [arr]:[]
                },
                rules: [
                    {
                        required: true,
                        message: ""
                    }
                ]
            },
            {
                type: "input",
                label: "",
                field: "name",
                value: "",
                props: {
                    placeholder: ""
                },
                rules: [
                    {
                        required: true,
                        message: ""
                    }
                ]
            },
            {
                type: "input",
                label: "",
                field: "idCardNo",
                value: "",
                props: {
                    placeholder: ""
                },
                rules: [
                    {
                        required: true,
                        message: ""
                    },
                    {
                        validator:(rule:any, value:any, callback:any)=>{
                            return new Promise(async (resolve, reject) => {
                                if(!isCardNo(value)){
                                    await reject('')
                                }else{
                                    await resolve(value)
                                }
                            })
                        }
                    }
                ]
            },
            {
                type: "select",
                label: "",
                field: "nation",
                value: "",
                props: {
                    placeholder: ""
                },
                asyncOptions: async () => {
                    const result = await getDictionaryOption('300029')
                    return result  
                },
                rules: [
                    {
                        required: true,
                        message: ""
                    }
                ]
            },
            {
                type: "date-pcker",
                label: "",
                field: "birthDate",
                value: "2020-12-29 09:23:14",
                props: {
                    placeholder: ""
                },
                rules: [
                    {
                        required: true,
                        message: ""
                    }
                ]
            },
            {
                type: "radio",
                label: "",
                field: "sex",
                value: '',
                options:[
                    {value:'M', label:''},
                    {value:'F', label:''}
                ],
                rules: [
                    {
                        required: true,
                        message: ""
                    }
                ]
            },
            {
                type: "input",
                label: "",
                field: "phone",
                value: "",
                props: {
                    placeholder: ""
                },
                rules: [
                    {
                        required: true,
                        message: ""
                    }
                ]
            },
            {
                type: "select",
                label: "",
                field: "maritalStatus",
                value: "",
                props: {
                    placeholder: ""
                },
                asyncOptions: async () => {
                    const result = await getDictionaryOption('300030')
                    return result  
                },
                rules: [
                    {
                        required: true,
                        message: ""
                    }
                ]
            },
            {
                type: "date-pcker",
                label: "",
                field: "joinArmyDate",
                value: "",
                props: {
                    placeholder: "",
                    onChange(date: any, dateString: string){
                        (formRef as any).current.setFieldsValue({
                            outArmyDate: null
                        })
                    },
                    
                }
            },
            {
                type: "date-pcker",
                label: "",
                field: "outArmyDate",
                value: "", 
                props: {
                    placeholder: "",
                    // disabled(){
                    //     return  (formRef as any).current.getFieldValue('conscriptsStartDate')? false:true
                    // },
                    disabledDate(current:string){
                        let startDate = (formRef as any).current.getFieldValue('joinArmyDate')
                        return current && current < (moment(startDate).endOf('day')  as any)
                    },
                }
            },
            {
                type: "date-pcker",
                label: "",
                field: "conscriptsStartDate",
                value: "",
                props: {
                    placeholder: "",
                    onChange(date: any, dateString: string){
                        (formRef as any).current.setFieldsValue({
                            conscriptsEndDate: null
                        })
                    },
                    
                }
            },
            {
                type: "date-pcker",
                label: "",
                field: "conscriptsEndDate",
                value: "", 
                props: {
                    placeholder: "",
                    // disabled(){
                    //     return  (formRef as any).current.getFieldValue('conscriptsStartDate')? false:true
                    // },
                    disabledDate(current:string){
                        let startDate = (formRef as any).current.getFieldValue('conscriptsStartDate')
                        return current && current < (moment(startDate).endOf('day')  as any)
                    },
                }
            },
            {
                type: "input-number",
                label: "",
                field: "conscriptsConvertDate",
                value: '',
                options:[],
            },
            {
                type: "input",
                label: "",
                field: "retireIdNumber",
                value: "",
                props: {
                    placeholder: ""
                },
            },
            {
                type: "input",
                label: "",
                field: "militaryCode",
                value: "",
                props: {
                    placeholder: ""
                },
            },
            {
                type: "radio",
                label: "",
                field: "ionelyOldPeople",
                value: '',
                options:[],
                asyncOptions: async () => {
                    const result = await getDictionaryOption('300020')
                    return result  
                },
            },
            {
                type: "select",
                label: "",
                field: "identificationBasis",
                value: "",
                props: {
                    placeholder: ""
                },
                asyncOptions: async () => {
                    const result = await getDictionaryOption('300008')
                    return result  
                },
            },
            {
                type: "select",
                label: "",
                field: "laborCapacity",
                value: "",
                props: {
                    placeholder: ""
                },
                asyncOptions: async () => {
                    const result = await getDictionaryOption('300006')
                    return result  
                },
            },
            {
                type: "select",
                label: "",
                field: "health",
                value: "",
                props: {
                    placeholder: ""
                },
                asyncOptions: async () => {
                    const result = await getDictionaryOption('300002')
                    return result  
                },
            },
            {
                type: "select",
                label: "",
                field: "abilityState",
                value: "",
                props: {
                    placeholder: ""
                },
                asyncOptions: async () => {
                    const result = await getDictionaryOption('300009')
                    return result  
                },
            },
            
            {
                type: "select",
                label: "",
                field: "employmentSituation",
                value: "",
                props: {
                    placeholder: ""
                },
                asyncOptions: async () => {
                    const result = await getDictionaryOption('300010')
                    return result  
                },
            },
            {
                type: "radio",
                label: "",
                field: "fiveGuarantees",
                value: '',
                options:[
                    {label:'', value:'1'},
                    {label:'', value:'0'},
                ],
            },
            {
                type: "radio",
                label: "",
                field: "otherPreferentialIdentity",
                value: '',
                options:[
                    {label:'', value:'1'},
                    {label:'', value:'0'},
                ],
            },
            {
                type: "select",
                label: "",
                field: "householdType",
                value: "",
                props: {
                    placeholder: ""
                },
                asyncOptions: async () => {
                    const result = await getDictionaryOption('300031')
                    return result  
                },
            },
            {
                type: "select",
                label: "",
                field: "householdAddressType",
                value: "",
                props: {
                    placeholder: ""
                },
                asyncOptions: async () => {
                    const result = await getDictionaryOption('300001')
                    return result  
                },
            },
            {
                type: "input",
                label: "",
                field: "workUnit",
                value: "",
                props: {
                    placeholder: ""
                },
            },
            {
                type: "select",
                label: "",
                field: "preferentialTreatmentType",
                value: "",
                props: {
                    placeholder: ""
                },
                asyncOptions: async () => {
                    const result = await getDictionaryOption('300011')
                    return result  
                },
            },
            {
                type: "date-pcker",
                label: "//",
                field: "cancelTime",
                value: "",
                props: {
                    placeholder: "//"
                }
            },
            {
                type: "radio",
                label: "",
                field: "subsistenceAllowances",
                value: "",
                props: {
                    placeholder: ""
                },
                options:[
                    {label:'', value:'y'},
                    {label:'', value:'n'},
                ]
            },
            {
                type: "input",
                label: "",
                field: "householdAddress",
                value: "",
                props: {
                    placeholder: ""
                },
                grid: {
                    rowGutter: 0,
                    colSpan: 24
                },
                tailLayout:{
                    labelCol: { span: 4 },
                    wrapperCol: { span: 18 },
                }, 
            },
            {
                type: "input",
                label: "",
                field: "actualAddress",
                value: "",
                props: {
                    placeholder: ""
                },
                grid: {
                    rowGutter: 0,
                    colSpan: 24
                },
                tailLayout:{
                    labelCol: { span: 4 },
                    wrapperCol: { span: 18 },
                }, 
            },
            {
                type: "textArea",
                label: "",
                field: "remarks",
                value: "",
                props: {
                    placeholder: ""
                },
                grid: {
                    rowGutter: 0,
                    colSpan: 24
                },
                tailLayout:{
                    labelCol: { span: 4 },
                    wrapperCol: { span: 18 },
                }, 
            },
        ]
    }

    const [fields, setFields] = useState(null as any)

    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    const [validateForm, setValidateForm] = useState(addSchema)

    useEffect(()=>{
       if(resData) setFields(resData.veteransBaseInfo)
    }, [])

  

    useImperativeHandle(ref, () => ({
        ...formRef.current,
    }))

    return (
        <>
            <NCustSchemaForm ref={formRef} fields={fields} dynamicValidateForm={validateForm} modalType={modalType} />
        </>
    )
})

export default BaseInfor