//
export const isCardNo = (card:string)=>{
    //1518151817X
    // const reg = /(^\d{15}$)|(^\d{17}(\d|X)$)/
    const reg = /^(^[1-9]\d{7}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}$)|(^[1-9]\d{5}[1-9]\d{3}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])((\d{4})|\d{3}[Xx])$)$/
    if(reg.test(card) === false){
        return false
    }
    return true
}