import { isObject, isString } from './is'
import moment from 'moment';

const DATE_TIME_FORMAT = 'YYYY-MM-DD HH:mm:ss';
const DATE_FORMAT = 'YYYY-MM-DD';

export function formatToDateTime(date: moment.MomentInput = null): string {
  return moment(date).format(DATE_TIME_FORMAT);
}

export function formatToDate(date: moment.MomentInput = null): string {
  return moment(date).format(DATE_FORMAT);
}

export const formatAgo = (str: string | number) => {
  if (!str) return '';
  const date = new Date(Number(str));
  const time = new Date().getTime() - date.getTime(); // - =  = 
  if (time < 0) {
    return '';
  } else if (time / 1000 < 30) {
    return '';
  } else if (time / 1000 < 60) {
    return parseInt(String(time / 1000)) + '';
  } else if (time / 60000 < 60) {
    return parseInt(String(time / 60000)) + '';
  } else if (time / 3600000 < 24) {
    return parseInt(String(time / 3600000)) + '';
  } else if (time / 86400000 < 31) {
    return parseInt(String(time / 86400000)) + '';
  } else if (time / 2592000000 < 12) {
    return parseInt(String(time / 2592000000)) + '';
  } else {
    return parseInt(String(time / 31536000000)) + '';
  }
};
/**
 * @description: 
 */
export function formatRequestDate(params: any) {
  for (const key in params) {
    if (params[key] && params[key]._isAMomentObject) {
      params[key] = params[key].format(DATE_TIME_FORMAT);
    }
    if (isString(key)) {
      const value = params[key];
      if (value) {
        try {
          params[key] = isString(value) ? value.trim() : value;
        } catch (error) {
          throw new Error(error);
        }
      }
    }
    if (isObject(params[key])) {
      formatRequestDate(params[key]);
    }
  }
}

export const dateUtil = moment;
