import React, {useState, useEffect, forwardRef, useContext} from 'react'
import { CustSchemaForm } from '@/components/form'
import {getDictionaryOption} from '@/api/system'
import {Context} from './allDetail'

const OtherInfor = forwardRef<any>((props, ref) => {
    const getContext = useContext(Context)
    const { modalType, resData } = getContext

    const addSchema = {
        layout : {
            labelCol: { span: 8 },
            wrapperCol: { span: 12 },
        },
        grid: {
            rowGutter: 10,
            colSpan: 12
        },
        formItem: [
            {
                type: "select",
                label: "",
                field: "houseType",
                value: "",
                props: {
                    placeholder: ""
                },
                asyncOptions: async () => {
                    const result = await getDictionaryOption('300025')
                    return result  
                },
            },
            {
                type: "select",
                label: "",
                field: "houseStatus",
                value: "",
                props: {
                    placeholder: ""
                },
                asyncOptions: async () => {
                    const result = await getDictionaryOption('300026')
                    return result  
                },
            },
            {
                type: "input",
                label: "",
                field: "houseArea",
                value: "",
                props: {
                    placeholder: ""
                },
            },
            {
                type: "input",
                label: "",
                field: "roomsNumber",
                value: "",
                props: {
                    placeholder: ""
                },
            },
            {
                type: "input",
                label: "",
                field: "familySize",
                value: "",
                props: {
                    placeholder: ""
                },
            },
            {
                type: "input",
                label: "18",
                field: "underAgeSize",
                value: "",
                props: {
                    placeholder: "18"
                },
            },
            {
                type: "input",
                label: "60",
                field: "agedSize",
                value: "",
                props: {
                    placeholder: "60"
                },
            },
        ]
    }

    const [fields, setFields] = useState(null as any)
    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    const [validateForm, setValidateForm] = useState(addSchema)

    useEffect(()=>{
        if(resData) setFields(resData.veteransHouseInfo)
    }, [])

    return (
        <CustSchemaForm ref={ref} fields={fields} dynamicValidateForm={validateForm} modalType={modalType}/>
    )
})

export default OtherInfor