import React, {useState, useEffect, forwardRef, useContext, useRef, useImperativeHandle} from 'react'
import { CustSchemaForm } from '@/components/form'
import {getOrganizationTtree, getDictionaryOption} from '@/api/system'
import { mapTree } from '@/utils/index'
import { FormInstance } from 'antd/lib/form'
import {Context} from '../allDetail'
import moment from 'moment'
import {isCardNo} from '@/utils/validate'

const BaseInfor = forwardRef<any>((props, ref) => {
    const getContext = useContext(Context) // context
    const { modalType, resData }:any = getContext
    const formRef =  useRef<FormInstance>(null)

    const addSchema = {
        layout : {
            labelCol: { span: 8 },
            wrapperCol: { span: 12 },
        },
        grid: {
            rowGutter: 10,
            colSpan: 12
        },
        formItem: [
            {
                type: "tree-select",
                label: "",
                field: "organizationId",
                value: undefined,
                loading: true,
                props: {
                    placeholder: "",
                    treeDefaultExpandAll: false,
                },
                asyncOptions: async (curValue:any, fields:any) => {
                    const {result}:any = await getOrganizationTtree() 
                    let arr = mapTree(result[0])
                    return arr? [arr]:[]
                },
                rules: [
                    {
                        required: true,
                        message: ""
                    }
                ]
            },
            {
                type: "input",
                label: "",
                field: "martyrLogo",
                value: "",
                props: {
                    placeholder: ""
                }
            },
            {
                type: "input",
                label: "",
                field: "name",
                value: "",
                props: {
                    placeholder: ""
                },
                rules: [
                    {
                        required: true,
                        message: ""
                    }
                ]
            },
            {
                type: "input",
                label: "",
                field: "nameUsedBefore",
                value: "",
                props: {
                    placeholder: ""
                },
            },
            {
                type: "date-pcker",
                label: "",
                field: "birthDate",
                value: "2020-12-29 09:23:14",
                props: {
                    placeholder: ""
                },
                rules: [
                    {
                        required: true,
                        message: ""
                    }
                ]
            },
            {
                type: "radio",
                label: "",
                field: "sex",
                value: '',
                options:[
                    {value:'M', label:''},
                    {value:'F', label:''}
                ],
                rules: [
                    {
                        required: true,
                        message: ""
                    }
                ]
            },
            {
                type: "input",
                label: "",
                field: "householdAddress",
                value: "",
                props: {
                    placeholder: ""
                },
            },
            {
                type: "input",
                label: "",
                field: "sacrificeNativePlace",
                value: "",
                props: {
                    placeholder: ""
                },
            },
            {
                type: "date-pcker",
                label: "",
                field: "revolutionaryTime",
                value: "",
                props: {
                    placeholder: ""
                }
            },
            {
                type: "select",
                label: "",
                field: "politicalIdentity",
                value: "",
                props: {
                    placeholder: ""
                },
                asyncOptions: async () => {
                    const result = await getDictionaryOption('301004')
                    return result  
                }
            },
            {
                type: "input",
                label: "",
                field: "livingUnit",
                value: "",
                props: {
                    placeholder: ""
                },
            },
            {
                type: "input",
                label: "",
                field: "previousPosition",
                value: "",
                props: {
                    placeholder: ""
                },
            },  
            {
                type: "date-pcker",
                label: "",
                field: "sacrificeDate",
                value: "",
                props: {
                    placeholder: ""
                }
            },
            {
                type: "input",
                label: "",
                field: "sacrificePlace",
                value: "",
                props: {
                    placeholder: ""
                },
            },  
            {
                type: "select",
                label: "",
                field: "burialPlaceType",
                value: "",
                props: {
                    placeholder: ""
                },
                asyncOptions: async () => {
                    const result = await getDictionaryOption('300033')
                    return result  
                },
            },
            {
                type: "input",
                label: "",
                field: "burialPlace",
                value: "",
                props: {
                    placeholder: ""
                },
            },
            {
                type: "textArea",
                label: "",
                field: "martyrsDeeds",
                value: "",
                props: {
                    placeholder: ""
                },
                grid: {
                    rowGutter: 0,
                    colSpan: 24
                },
                tailLayout:{
                    labelCol: { span: 4 },
                    wrapperCol: { span: 18 },
                }, 
            },
            {
                type: "textArea",
                label: "",
                field: "remarks",
                value: "",
                props: {
                    placeholder: ""
                },
                grid: {
                    rowGutter: 0,
                    colSpan: 24
                },
                tailLayout:{
                    labelCol: { span: 4 },
                    wrapperCol: { span: 18 },
                }, 
            },
        ]
    }

    const [fields, setFields] = useState(null as any)

    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    const [validateForm, setValidateForm] = useState(addSchema)

    useEffect(()=>{
       if(resData) setFields(resData.veteransBaseInfo)
    }, [])

  

    useImperativeHandle(ref, () => ({
        ...formRef.current,
    }))

    return (
        <>
            <CustSchemaForm ref={formRef} fields={fields} dynamicValidateForm={validateForm} modalType={modalType} />
        </>
    )
})

export default BaseInfor